html, body, #app {
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background: #DFE2DB;
}

#app {
  display: flex;
  justify-content: center;
  align-items: center;
}

@font-face {
  font-family: 'digital';
  src: url('../src/fonts/Calculator.ttf');
}

.container {
  padding: 0.5rem;
  background: #08163a;
  border-radius: 5px;
}

.grid {
  display: grid;
  grid-template-rows: 1.55fr 1fr 1fr 1fr 1fr;
  grid-gap: 0.25rem;
}

#display {
  font-family: 'digital';
  width: 18rem;
  grid-area: 1/1/2/6;
  font-size: 2.5rem !important;
  padding: 0.2rem 1.1rem;
  border-radius: 5px;
  border: 4px solid #7781aa;
  margin-bottom: 2px;
}

input, button {
  outline: none;
}

.text-right {
  text-align: right;
}

#clear {
  background: #b71010;
}

#zero {
  grid-area: 5/1/6/3;
}

button {
  font-family: 'digital';
  font-size: 1.2rem;
  color: white;
  background: #7781aa;
  border: none;
  border-radius: 5px;
}

button:hover {
  opacity: 0.85;
  cursor: pointer;
}

#add, #subtract, #multiply, #divide, #equals, #percent, #square-root {
  background: #666;
}

@media (max-width: 520px) {

  .container {
    width: 12rem;
  }

  .grid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1.55fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas: 
      "display display display display"
      "clear percent sroot divide"
      "seven eight nine multiply"
      "four five six subtract"
      "one two three add"
      "zero zero decimal equals"
  }
  
  #display {
    grid-area: 1/1/2/5;
    /* grid-area: display; */
    width: auto;
  }

  #clear {
    grid-area: clear
  }
  #percent {
    grid-area: percent
  }
  #square-root {
    grid-area: sroot
  }
  #divide {
    grid-area: divide
  }


  #seven {
    grid-area: seven
  }
  #eight {
    grid-area: eight
  }
  #nine {
    grid-area: nine
  }
  #multiply {
    grid-area: multiply
  }


  #four {
    grid-area: four
  }
  #five {
    grid-area: five
  }
  #six {
    grid-area: six
  }
  #subtract {
    grid-area: subtract
  }


  #one {
    grid-area: one
  }
  #two {
    grid-area: two
  }
  #three {
    grid-area: three
  }
  #add {
    grid-area: add
  }


  #zero {
    grid-area: zero
  }
  #decimal {
    grid-area: decimal
  }
  #equals {
    grid-area: equals
  }
 
  button {
    font-size: 1.2rem;
    color: white;
    background: #7781aa;
    border: none;
    border-radius: 5px;
  }
}


